* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;
}

#root {
  /* margin: 0, auto; */
  font-family: "Bebas Neue", cursive, sans-serif;
  color: darkgreen;
  // display: grid;
  // grid-template-rows: 10% 25% 25% 25% 10%;
  // grid-template-columns: 1fr 1fr 1fr 1fr;
  /* position: relative; */
  /* margin: 0, auto; */
  // height: 100vh;
  // width: 100vw;
}
.container {
  min-width: 80vw;
  min-height: 80vw;
  display: flex;
 flex-direction: column;
}
// body {
//   width: 100%;
//   margin: 5px, 5px, 5px, 5px;
// }

body:before {
  content: "";
  position: absolute;
  min-height: 600px;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background-image: url("https://i.imgur.com/u6uuS5Y.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 50%, 0;
  opacity: 0.5;
}

/* ///////  NAV  ///////////////////////// */
nav {
  grid-row: 1;
  grid-column: 1/5;
  background-color: #086217;
}


.right {
  justify-content: flex-end;
  cursor: pointer;
  li {
    padding: 5px;
    font-size: large;
  }
}


/* //////////// LANDING PAGE //////////////// */
.main-content {
  color: rgb(214, 199, 33);
}
.main-content-landing {
  grid-row: 2/5;
  grid-column: 2/4;
  display: flex;
  /* padding: 20px; */
  /* border: 5px solid black; */
  margin-bottom: 60px;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.main-content-landing > h1 {
  color: #086217;
}

